/*
 * action 
 */

export const GET_RE_TRACE_SLIDE_TITLE = Symbol('GET_RE_TRACE_SLIDE_TITLE');

/*
 * action 
 */
export const setRetraceSlideTitle = title => ({
    type: GET_RE_TRACE_SLIDE_TITLE,
    title
});


